@charset "UTF-8";
/*header*/
.dim {
	width:100vw; height:100vh; background-color:rgba(0,0,0,.8); position:fixed; top:0; left:0; z-index:2;
	display:none; opacity:0;
}
#header {
	width:100%; height:em(107); background-color:$white; position:relative; z-index:10;
	&:after { width:100%; height:em(1); content:""; position:absolute; top:em(106); background-color:#eee; }
	.gnb_btn {
		width:em(80); height:em(60); display:block; position:absolute; left:em(37); top:em(28);
		@include boxSizing;
		span {
			width:em(37); height:em(4); display:block; background-color:$black; margin:0 auto; position:relative;
			&:after, &:before {
				width:em(37); height:em(4); content:""; display:block; position:absolute;
				background-color:$black;
			}
			&:before { top:em(10); }
			&:after { bottom:em(10); }
		}
	}
	.lg_index_link {
		width:em(114); height:em(56); display:block; margin:0 auto; padding:em(25) 0;
		img { width:100%; }
	}
	.gnb_list {
		width:fit-content;
		width:em(620); height:calc(100vh - 5.35rem); position:relative; background-color:$white;
		left:-100vw;
		@include transition(left, .5s);
		&.active {left:0;}

		.main_title {
			width:em(370); display:block;
			a {
				display:block; padding:em(50) em(35); color:#999; font-weight:$bold; font-size:em(46);
				position:relative;
				@include boxSizing;
				&:after {
					width:em(20); height:em(30); content:""; display:none; position:absolute; right:em(50); top:50%;
					background:url(../../images/layout/active_icon.png) 0 0 no-repeat; background-size:cover;
					@include transform(translate(0,-50%));
				}
				&.main_title_active {
					color:$black;
					&:after {display:block;}
				}
			}
		}
		.sub_title {
			position:absolute; top:0; left:em(370);
			.sub_title_btn {
				width:em(250); text-align:center; padding:em(33) 0 em(37); font-size:em(32); color:#292929;
				@include boxSizing;
				button { font-weight:$medium; font-size:em(32); }
				&.subtitle_active {background-color:#eee;}
			}
			.subtitle_soundbar { padding:4px 16px !important; }
			.wtp3_btn { margin-left:13px; }
			.wtp3_btn_line { width:1px; height:16px; display:inline-block; background-color:#cdcdcd; }
		}
	}
	.goto_AV_link {
		position:absolute; bottom:em(40); left:em(40); font-size:em(32); font-weight:$bold; color:#222;
		&:after {
			width:em(24); height:em(24); margin:0 0 0 em(15);content:""; display:inline-block;
			background:url(../../images/layout/go_to_av_icon.png) 50% 50% no-repeat; background-size:cover;
		}
	}
	.close_btn {
		width:em(34); height:em(36); position:absolute; right:em(-65); top:em(35);
		background:url(../../images/layout/menu_close_btn.png) 0 0 no-repeat; background-size:cover;
	}
}

/*filter header*/
.filter_header {
	width:100%; height:114px; position:absolute; top:0; left:0; border-bottom:1px solid #ddd; background-color:$white;
	.filter_header_wrap {
        width:1600px; margin:0 auto; position:relative;
        @extend %clearfix;
		.lg_index_link {
			width: 114px;height: 56px;display: inline-block;position: absolute;left: 0;top: 30px; border: none;
			background: none; margin:0; padding:0;
			&:hover { background:none; }
		}
		img {float:left; padding:30px 0 0;}
		h1 {width:100%; position:absolute; top:0; left:0; text-align:center; font-size:50px; line-height:114px;}
		a {
			margin:40px 0 0; position:relative; z-index:1; float:right; padding:5px 40px 5px 20px; border:1px solid #df226b; border-radius:20px; font-size:18px; font-weight:$medium;
			background:url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/gotoAV_arrow.png") no-repeat 90% 50%;
			&:hover { color:$white; background:url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/gotoAV_arrow_active.png") no-repeat #df226b 90% 50%; }
			img { display:inline-block; margin:3px -10px 0 12px; }
		}
	}
}

/*footer*/
.footer {
	width:100%; height:46px; position:absolute; bottom:0; font-size:14px; background-color:#eeeeee;
	.privacy_law {
		display:inline-block; margin-left:160px; line-height:50px;
		.privacy {
			&:after { width:1px; height:10px; display:inline-block; margin:0 3px 0 5px; background-color:#010101; content:'';}
		}
	}
	p { width:72%; display:inline-block; text-align:center; }
}











