@charset "UTF-8";

.login_container {
    width:100%; height:100vh; position:relative; background-color:#f6fafb;
    &:before,
    &:after {display:block; content:""; position:absolute;}
    &:before {width:151px; height:383px; left:0; top:0; background-image:url(https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/login/login_bg01.jpg);}
    &:after {width:194px; height:277px; right:0; bottom:0; background-image:url(https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/login/login_bg02.jpg);}
    .login_wrap {
        width:1110px; height:575px; margin:0 auto; position:relative; top:50%;
        @include translate(0, -50%);
        @extend %clearfix;
    }
    .welcome_wrap {
        float:left;
        .login_logo {display:block;}
        .login_tit {padding:115px 0 0;}
        p {color:#906b78; line-height:28px; font-size:20px; padding:45px 0 0;}
    }
    .login_box {
        width:458px; height:575px; float:right; overflow:hidden;
        @include boxShadow(0 0 50px -25px);
        @include borderRadius(10px);
        .sign_txt {height:84px; padding:0 45px; font-size:26px; line-height:84px; background-color:#c3b8bc; color:#fff;}
        .input_wrap {
            padding:40px 45px 0 45px;
            .mail_box {
                padding:0 0 40px; border-bottom:2px solid #f0eff0; position:relative;
                @include boxSizing;
                &:after {
                    width:4px; height:2px; display:block; content:""; background-color:#f0eff0; border-left:8px solid $white; border-right:8px solid $white; position:absolute; bottom:-2px; left:50%;
                    @include translate(-50%, 0);
                }
                label {font-size:16px; color:#837e80; font-weight:$medium;}
                .input_box {
                    position:relative; margin:10px 0 0;
                    @extend %clearfix;
                    input[type=text] {
                        width:245px; height:32px; color:#010101; font-size:24px; font-weight:$medium; border-bottom:2px solid #dbd8d9; float:left;
                        @include transition(border-color, .2s);
                    }
                    p {color:#9e989b; font-size:24px; float:right;}
                }
                button {
                    width:100%; height:56px; margin:28px 0 0; background-color:#4e1223; color:$white; font-size:18px;
                    @include borderRadius(5px);
                }
            }
            .pass_box {
                padding:35px 0 0;
                label {font-size:18px; color:#837e80; font-weight:$medium;}
                .input_box {
                    position:relative; margin:10px 0 0;
                    @extend %clearfix;
                    input[type=text] {
                        width:364px; height:32px; color:#010101; font-size:24px; font-weight:$medium; border-bottom:2px solid #dbd8d9; float:left;
                        @include transition(border-color, .2s);
                    }
                }
                button {
                    width:100%; height:56px; margin:28px 0 0; background-color:#d83464; color:$white; font-size:18px;
                    @include borderRadius(5px);
                }
            }
        }
    }
}

