@charset "UTF-8";
/* Font style setting */
$Gordita:"Gordita", Arial, Apple SD Gothic Neo, sans-serif;

/* Point color setting */
$black:#000;
$white:#fff;

$red:#ef4247;

$gray1:#282e30;
$gray2:#292929;

/* Input - color setting */
$inp-bd-base:$gray1;

/* Button - color setting */
$btn-black:$black;

/* BASE SETTING */
$base-font: $Gordita;
$base-font-size:22px;
$base-font-color:$gray1;
$letter-spacing:0;
$light:300;
$regular:500;
$medium:600;
$bold:700;
$base-font-weight:$medium;

