@charset "UTF-8";
/*main container*/
#container {
    width:1600px; height:calc(100vh - 161px); margin:0 auto; background:url(../../images/main/main_bg.jpg) 50% 50% no-repeat;
    background-size:cover;
    @extend %clearfix;
    .img_3D_wrap{
        width:1050px; height:100%; position:relative; float:left;
        .goto3d_img {
            position:absolute; top:50%; left:50%;
            @include translate(-50%, -60%);
        }
        .btn_wrap {
            position:absolute; top:85%; left:50%; transform:translate(-50%, 0);
            button {
                width:236px; height:50px; line-height:46px; font-size:16px; font-weight:$bold;
                @include borderSet(2px, solid);
                @include boxSizing;
                @include transition(border-color, .2s);
                @include boxShadow(0px 10px 10px -14px $black);
                img {vertical-align:middle; margin:0 8px 0 0;}
            }
            .btn_3D {
                color:$white; border-color:#393939; background-color:#393939;
                &:hover {border-color:$white;}
            }
            .btn_simulate,
            .btn_gallery_popup {
                color:#292929; border-color:$white; background-color:$white;
                &:hover {border-color:#393939;}
            }
            .btn_gallery_popup {display:none;}
        }
    }
    //제품 카테고리별 그라데이션
    @mixin gradient_dot($color1, $color2){
        .model_title {background: linear-gradient(to right, $color1, $color2 30%);}
        .model_size_btn_active:after {background: linear-gradient(to right, $color1, $color2);}
        .key_features .hash_usp .hash_usp_btn_wrap button:before {background: linear-gradient(to bottom, $color1, $color2 50%);}
    }
    .model_info {
        width:510px; height:650px; position:relative; top:50%; float:right; margin:-323px 40px 0 0; padding:35px 0 0 35px;
        background-color:rgba(255,255,255,.5);
        @include boxSizing;
        @include boxShadow(0px 5px 25px -14px $black);

        //카테고리별 색상 적용
        &.OLED_Color_dot {
            @include gradient_dot(#ff5454, #881c8a);
        }
        &.QNED_Color_dot {
            @include gradient_dot(#d564b9, #4d99be);
        }
        &.NanoCell_Color_dot {
            @include gradient_dot(#0c5e88, #25c190);
        }
        &.UHD_Color_dot {
            @include gradient_dot(#0c5e88, #25c190);
        }
        &.Lifestyle_Color_dot {
            @include gradient_dot(#d564b9, #4d99be);
        }
        &.Soundbar_Color_dot {
            @include gradient_dot(#ff7777, #6f4ada);
        }
        &.Portable_Color_dot {
            @include gradient_dot(#ed42fc, #2291cc);
        }
        &.Gaming_Speaker_Color_dot {
            @include gradient_dot(#fc377d, #ec973f);
        }
        &.Earbuds_Color_dot {
            @include gradient_dot(#9540fd, #34ba9f);
        }
        //카테고리별 색상 적용 end

        .model_name {
            font-size:54px; font-weight:$medium;
            .model_title {display:block; font-size:26px; padding:0 0 10px; -webkit-background-clip: text; -webkit-text-fill-color: transparent;}
        }
        .model_size_btn {
            margin:45px 0 0; display:inline-block; padding:0 0 10px; position:relative; border-bottom:1px solid #e5e5e5;
            @extend %clearfix;
            &:before {width:100%; height:1px; display:block; content:""; position:absolute; bottom:0; left:0; background-color:#ccc;}
            button {
                min-width:73px; font-size:20px; font-weight:$bold; color:#959595; float:left; position:relative;
                &:after {
                    width:100%; height:7px; display:block; content:""; background-color:#ccc; position:absolute; bottom:-13px;
                    @include boxShadow(1px 1px 5px -2px #000);
                    @include borderRadius(3px);
                    @include opacity(0);
                    @include transition(opacity, .3s);
                }
            }
            .model_size_btn_active {
                color:$gray2;
                &:after {@include opacity(1)}
            }
            .model_size_btn_inactive {color:#cdcdcd;}

            .av_size_btn { width:160px; padding:5px 0; }
            .av_size_none { display:none; }
            .av_white { background-color:$white !important; color:$black; border:2px solid #d6d6d6; }
            .av_black { background-color:$black !important; }
            .av_beige { background-color:#eae4dd !important; color:$black; }
            .av_burgundy { background-color:#450015 !important; }
            .av_charcoal_black { background-color:$black !important; }
            .av_peacock_green { background-color:#1b3247 !important; }
            .av_haze_gold { background-color:#bdaf9c !important; }
            .av_pearl_white { background-color:$white !important; color:$black; border:2px solid #d6d6d6; }
        }
        .art90_color_btn_wrap{
            margin:30px 0 0; display:inline-block; padding:0 0 10px; position:relative; border-bottom:1px solid #e5e5e5;
            @extend %clearfix;
            &:before {width:100%; height:1px; display:block; content:""; position:absolute; bottom:0; left:0; background-color:#ccc;}
            button {
                width:100px; font-size:16px; font-weight:$bold; color:#111; float:left; position:relative; line-height:20px;
                &:after {
                    width:100%; height:7px; display:block; content:""; background-color:#ccc; position:absolute; bottom:-13px;
                    @include boxShadow(1px 1px 5px -2px #000);
                    @include borderRadius(3px);
                    @include opacity(0);
                    @include transition(opacity, .3s);
                }
                &.model_size_btn_active:after {
                    background: linear-gradient(to right, #9434e5, #d08246);
                    @include opacity(1);
                }
                &:nth-of-type(1):before {background-color:#ccc4b9;}
                &:nth-of-type(2):before {background-color:#505948;}
                &:nth-of-type(3):before {background-color:#704b43;}
                &:before {
                    width:15px; height:15px; display:block; content:""; border:1px solid $white;
                    position:absolute; top:65px; left:50%;
                    @include translate(-50%, 0);
                    @include boxSizing;
                    @include borderRadius(100%);
                }
            }


        }
        .key_features {
            margin:45px 0 0;
            .title {position:relative; font-size:24px; font-weight:$bold;}
            .hash_usp {
                margin:20px 0 0;
                .hash_usp_btn_wrap {
                    display:none;
                    button {
                        width:100%; height:58px; display:none; padding:0 0 0 20px; font-size:18px; font-weight:$bold; text-align:left; position:relative;
                        background-image:url(../../images/main/key_feature_btn_bg.png); border-bottom:1px solid $white;
                        @include boxSizing;
                        @include backgroundSoltC(0, 50%, no-repeat, 0 60px);
                        @include transition(all, .4s);
                        &:hover {
                            color:#292929; background-size:100% 60px;
                            @include boxShadow(0px 10px 10px -14px $black);
                            &:before {height:100%}
                            &:after {@include opacity(0);}
                        }
                        &:before {
                            width:5px; height:0; display:block; content:""; position:absolute; top:0; left:0;
                            @include transition(height, .2s);
                        }
                        &:after {
                            width:100%; height:1px; position:absolute; bottom:1px; left:0; display:block; content:""; background-color:#cdcdce;
                            @include transition(opacity, .2s);
                        }
                    }
                }
                .hash_usp_btn_wrap_av{
                    min-width:300px; display:none;
                    button {
                        height:26px; display:none; margin:0 0 8px 0; padding:2px 10px; font-size:17px; font-weight:$regular;
                        &:hover { color:$white; background-color:#df226b; border-radius:20px; font-weight:$regular; }
                        &:before { display:inline-block; width:6px; height:6px; margin:0 12px 4px 0; background-color:$black; border-radius:50%; content:''; }
                        &:hover:before { display:inline-block; width:6px; height:6px; margin:0 12px 4px 0; background-color:$white; border-radius:50%; content:''; }
                    }
                    #spotify_hifi,
                    #virtual_dolby_atmos,
                    #wireless_subwoofer {
                        cursor:initial;
                        &:hover {
                            color:$black; background-color:transparent; border-radius:20px; font-weight:$regular;
                            &:before {background-color:$black;}
                        }
                    }
                }
            }
            .hash_sub {
                display:none; color:#3b3b3b; margin-top:18px;
                button {
                    height:26px; display:none; margin:0 0 8px 0; padding:2px 10px; font-size:18px; font-weight:$regular;
                    &:hover { color:$white; background-color:#df226b; border-radius:20px; font-weight:$regular; }
                }
            }
        }
        .dimension {
            position:absolute; bottom:60px;
            .title {
                position:relative; font-size:23px; font-weight:$bold; color:#727272; margin:0 0 20px;
                .millimeter { display:inline-block; margin-left:4px; font-size:12px; color:#929292; }
            }
            .description_wrap{
                .description {
                    color:#6e6e6e; font-size:12px; font-weight:$bold; margin:10px 0 0;
                    .hyphen { display:inline-block; font-weight:400; letter-spacing:5px; vertical-align:baseline; }
                }
            }
        }
        .ex_description { position:absolute; bottom:25px; font-size:12px; color:#727272; font-weight:$bold;}
    }
    /*hashtag popup*/
    .hash_popup {
        width:100%; height:100%; display:none; position:absolute; left:0; top:0; background-color:rgba(0,0,0,0.7); z-index:10;
        .popup_subusp_btn {
            width:1600px; height:46px; position:absolute; top:50%; left:50%; transform:translate(-50%,-873%);
            button { border-radius:10px 10px 0 0; padding:12px 0 20px; }
            .popup_main_usp { width:400px; display:inline-block; font-size:20px; background-color:$black; color:$white; font-weight:$bold; cursor:initial; }
            .popup_sub_usp_wrap{
                width:1200px; display:flex; float:right;
                .popup_sub_usp { display:inline-block; flex:auto; justify-content:center; align-items:center; font-size:18px; background-color:#eee; color:#999999; font-weight:$medium; }
                .popup_sub_usp:nth-last-child(1) { margin-right:0; }
                .popup_sub_usp.active { background-color:$white; color:$black; }
            }
        }
        .popup_window {
            width:1600px; height:753px; display:none; position:absolute; top:50%; left:50%; transform:translate(-50%,-48%); border-radius:10px; background-color:$white;
            @extend %clearfix;
            .hash_info_wrap{
                display:none;
                .hash_info {
                    width:315px; position:absolute; float:left; padding:60px 50px 0;
                    .title {
                        font-size:34px; line-height:42px; font-weight:$medium; margin-bottom:30px;
                        .hide_title { display:none; }
                    }
                    .description { font-size:16px; font-weight:$regular; line-height:28px; color:#757575; }
                    .more_info_btn{
                        position:absolute; bottom:90px;
                        button { display:block; font-size:18px; width:200px; height:50px; }
                        .description { color:$white; background-color:$black; border-radius:40px; margin-bottom:10px; }
                        .watch_video { color:#999; border:1px solid #999; border-radius:40px; }
                    }
                }
                > img { position:absolute; right:0; }
                .right_contents { position:absolute; right:0; top:0; }
                .infinite_contrast_disclaimer { position:absolute; left:476px; bottom:10px; color:#757575; font-size:13px; line-height:20px; font-weight:$regular; }
                .popup_disclaimer01 { position:absolute; left:606px; bottom:10px; color:#757575; font-size:13px; line-height:20px; font-weight:$regular; }
                video { width:900px; height:633px; position:absolute; top:50%; right:152px; left:auto; transform:translateY(-50%); }
            }
            .popup_close_btn { width:28px; height:27px; position:absolute; top:-39px; right:-48px; background:url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/hash_close_icon.png") no-repeat 100% 50%; }
            &.popup_window_av { height:800px; }
        }
    }

    //3d popup
    .webgl_wrap {
        width:100vw; height:100vh; top:0; left:0; position:fixed; z-index:10; display:none;
        &:after {width:100%; height:100%; display:block; content:""; position:fixed; top:0; left:0; background-color:rgba(0,0,0,.6);}
        .webgl_close_btn { width: 42px; height: 43px; position: absolute; top:50%; left:50%; transform:translate(775px, -414px); background: url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/3d_close_icon.png") no-repeat 100% 50%; z-index:100;}
    }
    #root {width:1600px; height:800px; position:fixed; top:50%; left:50%; transform:translate(-50%, -50%); z-index:100; overflow:hidden;}

    //simulator popup
    .simulator_popup {
        width:100vw; height:100vh; top:0; left:0; position:fixed; z-index:10; background-color:rgba(0,0,0,.5); display:none;
        @include opacity(0);
        .simulator_close_btn {width: 42px; height: 43px; position:absolute; top:50%; left:50%; transform:translate(775px, -414px); background: url("https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/main/3d_close_icon.png") no-repeat 100% 50%; z-index:100;}
        .simulator_wrap {
            width:1600px; height:800px; position:absolute; top:50%; left:50%;
            @include translate(-50%, -50%);
        }
        .simulator_bg {position:absolute; top:0; left:0;}
        .simulator_controller {
            position:absolute; top:20px; left:20px;
            .controller_wrap {width:325px; overflow:hidden; float:left;}
            .controller_close_btn {
                width:42px; height:42px; float:left; background-color:#111; position:relative;
                &.active:after {
                    @include transform(translate(-55%, -50%) rotate(225deg));
                }
                &:after {
                    width:9px; height:9px; display:block; content:""; position:absolute; top:50%; left:50%; border-left:3px solid $white; border-bottom:3px solid $white;
                    @include transform(translate(-45%, -50%) rotate(45deg));
                    @include transition;
                }
            }
            .controller_box {width:305px; background-color:#111; padding:0 10px;}
            .interior_box {
                border-bottom:1px solid #333;
                strong {color:$white; display:block; font-size:20px; font-weight:$medium; border-bottom:1px solid #333; padding:10px 0 10px 14px;}
                ol {padding:0 0 20px 14px;}
                li {padding:14px 0 0;}
                button {
                    @extend %clearfix;
                    &.active,
                    &:hover {
                        .img_wrap:after {@include opacity(1);}
                    }
                }
                .img_wrap {
                    width:160px; height:80px; overflow:hidden; float:left; position:relative;
                    &:after {
                        width:160px; height:80px; display:block; content:""; position:absolute; top:0; left:0; border:6px solid #bf9245;
                        @include boxSizing;
                        @include opacity(0);
                        @include transition(opacity, .2s);
                    }
                    img {width:100%;}
                }
                p {float:left; color:$white; font-size:16px; font-weight:$regular; padding:64px 0 0 10px;}
            }
            .installation_box {
                border-bottom:1px solid #333;
                strong {color:$white; display:block; font-size:20px; font-weight:$medium; border-bottom:1px solid #333; padding:10px 0 10px 14px;}
                .installation_btn_wrap {
                    padding:15px 9px; display:flex;
                    @extend %clearfix;
                    button {
                        width:120px; height:75px; color:#333; font-size:12px; background-color:#cdcdcd; padding:50px 0 0; margin:0 5px;
                        @include backgroundSolt(50%, 10px);
                        @include transition(background-color, .2s);
                        &.active,
                        &:hover {background-color:#bf9245;}
                        &.wide_stand_btn {background-image:url(../../images/main/simulator/wide_stand_icon.png);}
                        &.wall_mount_btn {background-image:url(../../images/main/simulator/wall_mount_icon.png);}
                        &.stand_btn {background-image:url(../../images/main/simulator/stand_icon.png)}
                    }
                }
            }
            .infill_on_off {
                padding:15px 14px 20px;
                @extend %clearfix;
                p {font-size:15px; font-weight:$medium; float:left; color:$white;}
                button {
                    width:56px; height:18px; background-color:#75716a; position:relative; float:right;
                    @include borderRadius(9px);
                    @include transition(background-color, .2s);
                    &:hover,
                    &.active {background-color:#bf9245;}
                    &.active:after {@include translate(32px, 0);}
                    &:after {
                        width:24px; height:24px; display:block; content:""; position:absolute; left:0; top:-3px; background-color:$white;
                        @include borderRadius(100%);
                        @include transition;
                    }
                }
            }
            .ok_btn_wrap {
                padding:0 14px 20px 14px;
                .simulator_ok_btn {
                    width:100%; height:52px; border:4px solid #bf9245; font-size:22px; color:#bf9245; font-weight:$bold;
                    @include boxSizing;
                    @include transition;
                    &:hover {background-color:#bf9245; color:$white;}
                }
            }
            .simulate_down_wrap {
                padding:10px 20px; background-color:$black;
                @extend %clearfix;
                button {
                    color:$white; line-height:30px; font-size:13px; float:right; padding:0 35px 0 0; background-image:url(../../images/main/simulator/image_down_icon.gif);
                    @include backgroundSolt(100%, 0, no-repeat);
                }
            }
        }
    }
}

