@charset "UTF-8";

@import "_common/base";
@import "_layout";
@import "_intro";
@import "_login";
@import "_main";

/*
  z-index 가이드
  header : 200
  gnb : 210
  container : auto
*/

//기본 스타일
input[type=text],
textarea,
select {
   outline:none;
   @include transition(border-color, .2s);
   &:focus { border-color:$red !important;}
}

::selection { background:$red; color:$white; text-shadow:none; }
::-moz-selection { background:$red; color:$white; text-shadow:none; }
img { vertical-align:top; image-rendering:-webkit-optimize-contrast;}
caption { width:1px; height:1px; overflow:hidden; margin:-1px;}
html,body {overflow:visible !important;}
