@charset "UTF-8";

.intro_container {
    width:100%; height:calc(100% - 46px); background-image:url(https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/intro/intro_bg.jpg);
    @include displayFlexCenter;
    @include backgroundSoltC;
    .intro_tit {text-align:center;}
    .link_wrap {
        width:1400px; margin:0 auto; padding:60px 0 0;
        @extend %clearfix;
        .tv_link {
            position:relative; margin:90px 0 0; float:left;
            &:after {width:675px; height:559px; content:""; display:block; border-bottom-left-radius:55%; position:absolute; top:-22px; left:-22px; border:2px solid #e2d3c4;}
            a {
                width:675px; height:559px; display:block; border-bottom-left-radius:55%; position:relative; z-index:1; overflow:hidden;
                @include boxShadow(0 0 50px -25px);
                img {@include transition(all, .5s);}
                &:after {
                    width:100%; height:100%; display:block; content:""; background-color:rgba(0, 0, 0, .5); position:absolute; top:0; left:0;
                    @include opacity(0);
                    @include transition(opacity, .5s);
                }
                &:before {
                    width:246px; height:131px; display:block; content:""; position:absolute; top:50%; left:50%; background:url(https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/intro/tv_over.png) 50% 50% no-repeat; z-index:1;
                    @include translate(-50%, -50%);
                    @include opacity(0);
                    @include transition(opacity, .5s);
                }
                &:hover {
                    img {@include scale(1.2)}
                    &:before,
                    &:after {@include opacity(1);}
                }
            }
        }
        .av_link {
            position:relative; float:right;
            &:after {width:675px; height:559px; content:""; display:block; border-top-right-radius:55%; position:absolute; top:-22px; left:22px; border:2px solid #e2d3c4;}
            a {
                width:675px; height:559px; display:block; border-top-right-radius:55%; position:relative; z-index:1; overflow:hidden;
                @include boxShadow(0 0 50px -25px);
                img {@include transition(all, .5s);}
                &:after {
                    width:100%; height:100%; display:block; content:""; background-color:rgba(0, 0, 0, .5); position:absolute; top:0; left:0;
                    @include opacity(0);
                    @include transition(opacity, .5s);
                }
                &:before {
                    width:246px; height:131px; display:block; content:""; position:absolute; top:50%; left:50%; background:url(https://d1wl83prm6u37g.cloudfront.net/lgvirtualstudio-2021/images/intro/av_over.png) 50% 50% no-repeat; z-index:1;
                    @include translate(-50%, -50%);
                    @include opacity(0);
                    @include transition(opacity, .5s);
                }
                &:hover {
                    img {@include scale(1.2)}
                    &:before,
                    &:after {@include opacity(1);}
                }
            }
        }
    }
}
.confidentiality_popup_wrap {
    width:100%; height:100%; position:absolute; top:0; left:0; background-color:rgba(0, 0, 0, 0.6); z-index:1;
    .confidentiality_popup {
        width: 870px; height:244px; position:absolute; top:50%; left:50%; transform:translate(-50%,-50%); padding:98px 0; line-height:63px; text-align:center; border-radius:10px; background-color:$white;
        .confidentiality_title { font-size:40px; color:#ad1948; font-weight:$bold; }
        .confidentiality_text { font-size:16px; font-weight:$light; line-height:28px; }
    }
    .confidentiality_popup_close_btn { position:absolute; top:-6px; right:-56px; }
}